body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: -webkit-fill-available;

}
html, body {
  height: -webkit-fill-available;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.game-container {
  height: 98vh; 
}

.carousel {
  display: flex;
  align-items: center;
  width:100vw;
  overflow: scroll;
  background-color: rgb(164, 164, 164);
  padding-block: 0.5em;
  height: 4em;
}

.card-container {
  height:calc(100% - (7em + 1em));
  width:100vw;
  /* background-color: rgb(255, 255, 255); */
}

.controls {
  width:100vw;
  height: 3em;
  display: flex;
  flex-direction: column;
  /* background-color: purple; */
  justify-content: center;
}

.control-buttons-container {
  display: flex;
  overflow: scroll;
  height:100%;
}

.carousel-image {
  height:100%;
  aspect-ratio: 3/4;
  object-fit: contain;
}

.card-progress {
  height:0.5em;
  background-color: rgb(115, 115, 115);
}

.card-progress-container {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  height:2em;
  width:100vw;
  /* background-color: rgb(221, 221, 221); */
}

button {
  height:100%;
  border-radius: 0px;
  border:none;
}


.settings-open {
width:8em;
height:4em;
/* border: none; */
}

.settings-close {
  width:100%;
  height:3em;
}

.settings {
  position: fixed;
  top:50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 1em;
  width:90vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.velocity-button {
  font-size: 3em;
  background-color: transparent;
  border: none;
}

.velocity-container {
  width:100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: 2em;
}

/* .grid-container {
  height:80vh;
  padding:1vh;
} */

.card {
  perspective: 1000px;
  height:100%;
  max-width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: blue; */
}

.card img{
  /* perspective: 1000px; */
  object-fit: contain;
  flex:1;
  height:90%;
  /* width:100%; */
  max-width:90vw;
  /* max-height: 70vh; */
  transition: transform 0.5s;
  transform-style: preserve-3d;
}

.card.flipping img {
  transform: rotateY(180deg);
}

.hidden {
  display: none;
}

.play-button {
  background-color: rgba(255, 255, 255, 0.931);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  border-radius: 50%;
  left:calc(50% - 2em);
  height:4em;
  width:4em;
  box-shadow: 0px 0px 4px;
  color:rgba(155, 155, 155, 0.608);
}

.play-button img {
  /* width:100%; */
  height:50%;
  object-fit: contain;
}

.settings-button {
  background-color: rgba(255, 255, 255, 0.931);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  border-radius: 50%;
  left:calc(50% - 1em + 5em);
  height:2em;
  width:2em;
  box-shadow: 0px 0px 4px;
  color:rgba(155, 155, 155, 0.608);
}

.settings-button img {
  /* width:100%; */
  height:50%;
  object-fit: contain;
}

.menu {
  position: fixed;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  width:100vw;
  left:-100vw;
  height:100vh;
  z-index: 10;
  background-color: white;
  transition: left 0.5s ease-in-out;
}

.open {
  left:0;
}

.menu .exit {
  height:2em;
  width:2em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  color: black;
  margin: 1em;
}

.menu .exit:hover {
  cursor: pointer;
}


.menu-item {
  width:100%;
  height:2em;
  margin-bottom: 1em;
}